import Link from 'next/link';
import Image from 'next/image';

/* Import media */
import PosterCard3BgSvg from '@/media/images/global/poster_card_3.svg';

/* Import styles */
import styles from './poster_card_3-pagePart.module.scss';

/* Define data types */
interface Props {
  infoP?: {
    heading: string;
    text: string;
    links: {
      text: string;
      href: string;
      value: string;
    }[];
    imageSrc?: string;
    imageAlt?: string;
  };
}

/**
 * PosterCard3PagePart Component
 *
 * A reusable poster card component that displays a heading, text,
 * action links, and an optional image.
 *
 * @param infoP - Optional information for the card.

 * @example
 * ```tsx
 * <PosterCard3PagePart
 *   infoP={{
 *     heading: "Connect with Global Experts",
 *     text: "Join a network of over 4,000 mentors worldwide.",
 *     links: [
 *       { text: "Find a Mentor", href: "/mentors", value: "mentor_list" },
 *       { text: "Join as a Mentor", href: "/apply", value: "mentor_apply" }
 *     ],
 *     imageSrc: "/images/mentors.png",
 *     imageAlt: "Group of mentors"
 *   }}
 * />
 * ```
 */
const PosterCard3PagePart: React.FC<Props> = ({ infoP }) => {
  // Define default information for the card
  const defaultInfo = {
    heading: 'Export more than 4,000 Mentors from 50+ countries',
    text: 'To help you achieve and overcome any challenges you face.',
    links: [
      {
        text: 'Browse all',
        href: '#',
        value: 'data value',
      },
      {
        text: 'Browse all',
        href: '#',
        value: 'data value',
      },
    ],
    imageSrc: PosterCard3BgSvg,
    imageAlt: "mentor's faces",
  };

  // Use provided infoP or fallback to defaultInfo
  const info = infoP || defaultInfo;

  return (
    <div className={`${styles.poster_card_3_pagePart}`}>
      {/* Information container */}
      <div className={`${styles.info_cont}`}>
        <h2 className={`${styles.heading}`}>{info.heading}</h2>
        <p className={`${styles.text}`}>{info.text}</p>

        {/* Render action links */}
        <div className={`${styles.info_cont__button_cont}`}>
          {info.links.map((item, index) => (
            <Link
              key={index}
              href={item.href}
              className={`${styles.info_cont__button_cont__button}`}
              data-value={item.value}
            >
              {item.text}
            </Link>
          ))}
        </div>
      </div>

      {/* Image container */}
      <div className={`${styles.image} image_container_type_1`}>
        <Image
          src={info.imageSrc ?? defaultInfo.imageSrc}
          alt={info.imageAlt ?? defaultInfo.imageAlt}
          width={120}
          height={80}
        />
      </div>
    </div>
  );
};

PosterCard3PagePart.displayName = 'PosterCard3PagePart';

export default PosterCard3PagePart;
