import Link from 'next/link';
import Image from 'next/image';

/* Import medias */
import MicrosoftLogo from '@/media/images/global/companies_logo/microsoft-logo-svgrepo-com.svg';
import CocaLogo from '@/media/images/global/companies_logo/coca-cola-logo-svgrepo-com.svg';
import MySqlLogo from '@/media/images/global/companies_logo/mysql-logo-svgrepo-com.svg';
import PiLogo from '@/media/images/global/companies_logo/raspberry-pi-logo-svgrepo-com.svg';
import ReactLogo from '@/media/images/global/companies_logo/react-logo-svgrepo-com.svg';

/* Import styles */
import styles from './sponsor_cards_1-pagePart.module.scss';

/* Define data type of props */
interface SponsorInfo {
  heading: string;
  list: {
    imgSrc: string;
    imgAlt: string;
    link: string;
    value: string;
  }[];
}

interface Props {
  infoP?: SponsorInfo;
}

/**
 * This component supports passing additional styles via props and displays a default label if none is provided.
 *
 * @param infoP - Pass info here
 *
 * @example
 * ```tsx
 * <SponsorCards1PagePart
 *   infoP={{ heading: "Our Sponsors", list: [{ imgSrc: MicrosoftLogo, imgAlt: "Microsoft", link: "#", value: "Microsoft" }] }}
 * />
 * ```
 */

const SponsorCards1PagePart: React.FC<Props> = ({ infoP }) => {
  const defaultInfo: SponsorInfo = {
    heading: 'Helping 11,000+ global companies command their attack surface',
    list: [
      {
        imgSrc: MicrosoftLogo,
        imgAlt: 'Microsoft',
        link: '#',
        value: 'Microsoft',
      },
      { imgSrc: CocaLogo, imgAlt: 'Coca-Cola', link: '#', value: 'Coca-Cola' },
      { imgSrc: MySqlLogo, imgAlt: 'MySQL', link: '#', value: 'MySQL' },
      {
        imgSrc: PiLogo,
        imgAlt: 'Raspberry Pi',
        link: '#',
        value: 'Raspberry Pi',
      },
      { imgSrc: ReactLogo, imgAlt: 'React', link: '#', value: 'React' },
    ],
  };

  const sponsors = infoP || defaultInfo;

  return (
    <div className={styles.sponsor_cards_1_pagePart}>
      <h2>{sponsors.heading}</h2>
      <div className={styles.list}>
        {sponsors.list.map((sponsor, index) => (
          <Link key={index} href={sponsor.link} className={styles.link}>
            <Image
              src={sponsor.imgSrc}
              alt={sponsor.imgAlt}
              data-value={sponsor.value}
              priority={index === 0} // Prioritize first image for performance
              className={styles.image}
            />
          </Link>
        ))}
      </div>
    </div>
  );
};

SponsorCards1PagePart.displayName = 'SponsorCards1PagePart';

export default SponsorCards1PagePart;
